.InfoPanel {
  -ms-flex-align: center;
      align-items: center;
  border-radius: 6px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.InfoPanel > * {
  border-radius: 100%;
  font-size: 18px;
  height: 90px;
  margin: 5px;
  width: 90px;
}

.InfoPanel .player {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.InfoPanel .player div {
  font-size: 12px;
}

.InfoPanel button {
  background: #aaa;
  border-radius: 100%;
  border: 0;
  color: #000;
  cursor: pointer;
  outline: 0;
  width: 90px;
  height: 90px;
  -webkit-transition: -webkit-box-shadow .3s;
  transition: -webkit-box-shadow .3s;
  -o-transition: box-shadow .3s;
  transition: box-shadow .3s;
  transition: box-shadow .3s, -webkit-box-shadow .3s;
}

.InfoPanel button:hover {
  -webkit-box-shadow: 0 0 11px rgba(33,33,33,.6);
          box-shadow: 0 0 11px rgba(33,33,33,.6); 
}


